@use '../util' as *;

.services {

    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: rem(30);
    }

    &__item {
        display: flex;
        align-items: center;
        max-width: rem(555);
        max-height: rem(180);
        padding: rem(24) rem(30) rem(20) rem(20);
        background-color: var(--black);
        border-radius: rem(20);

        @include breakpoint-down(large) {
            flex-direction: column;
            text-align: center;
            max-height: 100%;
        }

        img {
            margin-right: rem(20);
            position: relative;
            top: rem(-20);
        }
    }

    .title--md::before {
        content: 'Services';
    }
}